import { useEffect, useState } from "react";
import { withRouter } from "react-router";
import { Button, Header, Icon, Joke } from "../components";

const Search = (props) => {
  const [isLoading, setIsLoading] = useState(true);
  const [jokes, setJokes] = useState();

  const getJokes = () => {
      setIsLoading(true)
      fetch(
        `https://api.chucknorris.io/jokes/random?category=${props.location.state.category}`
      )
        .then((res) => res.json())
        .then((json) => setJokes(json.value))
        .catch((err) => console.log(err))
        .finally(() => setIsLoading(false));
  }

  useEffect(() => {
      getJokes()
  }, []);

  return (
    <div className="wrapper">
      <Header />
      <section className="container">
        <div style={{ marginTop: '2rem', marginBottom: '1rem', textAlign: 'center' }}>
          <Icon />
        </div>
        <span
          style={{
            color: "var(--primary)",
            fontWeight: "600",
            textAlign: 'center',
            marginBottom: '2rem'
          }}
        >
          Category: {props.location.state.category}
        </span>
        <center>
          <Joke text={jokes} />
          <div style={{ marginTop: '1.5rem' }}>
            <Button text={isLoading ? `getting random ${props.location.state.category} jokes` : "Another!"} onClick={getJokes} disabled={isLoading} />
          </div>
        </center>
      </section>
    </div>
  );
};

export default withRouter(Search);
