import './input.css'

const Input = (props) => {
    return (
        props.type === 'select' ? (
            <select className="input" placeholder={props.placeholder} value={props.value} onChange={props.onChange}>
                <option value="" disabled hidden>Search jokes by category</option>
                {props.options && props.options.map(option => (
                    <option key={option} value={option} style={{ fontWeight: 600 }}>{option}</option>
                ))}
            </select>
        ) : (
            <input type={props.type} className="input" placeholder={props.placeholder} value={props.value} onChange={props.onChange} />
        )
    )
}

export default Input