import { withRouter } from 'react-router'
import { Link } from 'react-router-dom'
import './header.css'
import icon from '../../assets/images/Icon.png'

const Header = (props) => {
    return (
        <header>{props.location.pathname !== '/' ? <Link to="/"><img src={icon} alt="back icon" /></Link> : <span>&nbsp;</span>} chuck norris <span>&nbsp;</span></header>
    )
}

export default withRouter(Header)