import { useEffect, useState } from "react";
import { withRouter } from "react-router";
import { Button, Header, Icon, Input, Joke } from "../components";

const Home = (props) => {

  const [isLoading, setIsLoading] = useState(true)
  const [searchValue, setSearchValue] = useState('')
  const [categoryValue, setCategoryValue] = useState('')
  const [jokes, setJokes] = useState()
  const [categories, setCategories] = useState()

  const getRandomJokes = () => {
    setIsLoading(true)
    fetch('https://api.chucknorris.io/jokes/random')
      .then(res => res.json())
      .then(json => setJokes(json.value))
      .catch(err => console.log(err))
      .finally(() => setIsLoading(false))
  }

  const getCategories = () => {
    fetch('https://api.chucknorris.io/jokes/categories')
      .then(res => res.json())
      .then(json => setCategories(json))
      .catch(err => console.log(err))
  }
  
  useEffect(() => {
    getRandomJokes()
    getCategories()
  }, [])

  return (
    <div className="wrapper">
      <Header />
      <section className="container">
          <div style={{ display: 'flex', gap: "1rem", width: '100%' }}>
              <Input type="text" placeholder="Search jokes by text" value={searchValue} onChange={val => setSearchValue(val.target.value)} />
              <Button text="Search!" onClick={() => props.history.push({pathname: '/search', state: {keyword: searchValue}})} disabled={!searchValue} />
          </div>
          
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', flex: 1 }}>
            <div style={{ marginTop: '2rem', marginBottom: '1.5rem' }}>
              <Icon/>
            </div>

            <Joke text={jokes} />
            <div style={{ marginTop: '1.5rem' }}>
            <Button text={isLoading ? "getting random jokes" : "Another!"} onClick={getRandomJokes} disabled={isLoading} />
            </div>
          </div>

          <div style={{ display: 'flex', gap: "1rem", width: '100%' }}>
              <Input type="select" placeholder="Search jokes by category" value={categoryValue} options={categories} onChange={val => setCategoryValue(val.target.value)} />
              <Button text="Search!" onClick={() => props.history.push({pathname: '/category', state: {category: categoryValue}})} disabled={!categoryValue} />
          </div>
      </section>
    </div>
  );
};

export default withRouter(Home);
