import { Route, Switch } from "react-router"
import { Category, Home, Search } from "../screens"

const Main = () => {
    return (
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/search">
            <Search />
          </Route>
          <Route exact path="/category">
            <Category />
          </Route>
        </Switch>
    )
}

export default Main