import { useEffect, useState } from "react";
import { withRouter } from "react-router";
import { Header, Icon, Joke } from "../components";

const Search = (props) => {
  const [isLoading, setIsLoading] = useState(true);
  const [jokes, setJokes] = useState();

  const getJokes = () => {
      setIsLoading(true)
      fetch(
        `https://api.chucknorris.io/jokes/search?query=${props.location.state.keyword}`
      )
        .then((res) => res.json())
        .then((json) => json.result && setJokes(json.result))
        .catch((err) => console.log(err))
        .finally(() => setIsLoading(false));
  }

  useEffect(() => {
      getJokes()
  }, []);

  return (
    <div className="wrapper">
      <Header />
      <section className="container" style={{ overflow: 'auto' }}> 
        <div style={{ marginTop: '2rem', marginBottom: '1rem', textAlign: 'center' }}>
          <Icon />
        </div>
        <span
          style={{
            color: "var(--primary)",
            fontWeight: "600",
            textAlign: 'center',
            marginBottom: '2rem'
          }}
        >
          Search Text: {props.location.state.keyword}
        </span>
        <div>
          {isLoading ? (
            <Joke text="getting jokes" />
          ) : (
            jokes.map((joke) => <div key={joke.id} style={{ marginBottom: '1rem' }}><Joke text={joke.value} /></div>)
          )}
        </div>
      </section>
    </div>
  );
};

export default withRouter(Search);
